@import'../common/styles/sass/variables.scss';
@import '../common/components/button/Button.module.scss';
@import'../common/styles/sass/mixins.scss';

.contactBlock {
    min-height: 70vh;
    background-color: $bgColor;
   
.contacts {
    @include container();
        padding: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        
   
}
.formBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    min-height: 130px;
    margin-bottom: 20px;
    button[type='submit'] {
        @extend .btn;
        align-self: center;
        background-color: transparent;
      
    }

    @media screen and(max-width: 372px) {
        width: 100%
    }

    .formArea,
    .messageArea {
        background: $bgSecondColor;
        border: 1px solid #4a4a4a;
        box-sizing: border-box;
        outline: none;
        color: $primaryColor;
        margin-bottom: 1.2em;

        &:-webkit-autofill {
            border: 1px solid #4a4a4a;
            -webkit-text-fill-color: #495057;
            -box-shadow: 0 0 0 1000px #252527 inset;
            transition: background-color 5000s ease-in-out 0s;

            &:focus {
                border: 1px solid $bgSecondColor;
            }


        }


    }

    .formArea {
        height: calc(1.5em + .75rem + 2px);
        padding: .4rem 15px;

        &:focus {
            border: 1px solid transparent;
            transition: all 0.4s ease-in-out;
        }
    }

    .messageArea {
        min-height: 160px;
        padding: 15px;
        resize: none;

        &:focus {
            border: 1px solid transparent;
            transition: all 0.4s ease-in-out;
        }
    }

    button[type='submit'] {
        @extend .btn;
        align-self: center;
        background-color: transparent;
        margin-top: 30px;
        text-decoration: underline;
    }
}
}