@import'../common/styles/sass/variables.scss';
@import'../common/styles/sass/mixins.scss';


.footerBlock {
    min-height: 20vh;
    background-color: $bgSecondColor;

    .container {
        @include container();
        display: flex;
        flex-direction: column;
        align-items: center;

        h2 {
            color: $textColor;
            letter-spacing: 2px;
        }

        .icons {
            width: 280px;
            display: flex;
            justify-content: space-between;
            margin-bottom: 20px;

            .icon {
                height: 50px;
                width: 50px;
                color: $textColor;
                font-size: 30px;
            }
        }

        .copyright {
            text-align: center;
            color: $primaryColor;
            
        }
    }
}




