@import'../common/styles/sass/variables.scss';
@import'../common/styles/sass/mixins.scss';

.mainBlock {
    height: 100vh;
    background-color: $bgColor;
    color: $textColor;
    @include flex(row, center, center, nowrap);
    padding: 100px 0;

    .container {
        @include container();
        @include flex(row, space-around, center, wrap);

        .greeting {
            @include flex(column, flex-start, flex-start, nowrap);

            span {
                font-size: 16px;

                &:nth-child(2) {
                    @include text($titleFontFamily, 50px, 1.2, 700);
                    letter-spacing: 5px;
                    margin: 10px 0;

                    span {
                        @include text($titleFontFamily, 50px, 1.2, 700);
                        letter-spacing: 5px;
                        position: relative;
                        z-index: 0;
                        display: inline-block;

                        &:before {
                            content: '';
                            position: absolute;
                            width: 100%;
                            height: 10px;
                            background: $primaryColor;
                            bottom: 0;
                            z-index: -1;
                        }
                    }
                }
            }

            h1 {
                @include text($textFontFamily, 20px, 1.2, 400);
                margin: 0;
                color: $primaryColor;
                }

            @media screen and (max-width: 1143px) {
                align-items: center;

            }
        }

        .photo {
            position: relative;
            z-index: 0;

            @media screen and (max-width: 1143px) {
                margin-top: 50%;
            }
        }

        .image {
            height: 400px;
            width: 300px;
            background-image: url('./../assets/image/myFoto.png');
            background-position: 80% 0;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;

            @media screen and (max-width: 600px) {
                heigth: 360px;
                width: 260px;
                transition: .5s;
            }

            &:before {
                content: '';
                position: absolute;
                width: 103%;
                height: 110%;
                border: 5px solid $primaryColor;
                left: 15px;
                top: -20px;
                z-index: -1;
            }
        }
    }
    }





